.investors-partners {
    margin-bottom: 62px;
    padding-top: 58px;
    &__title {
        color: rgba(#16009e, 0.9);
        margin: 0;
        margin-bottom: 79px;
        text-align: center;
        @media(max-width: 767px) {
            margin-bottom: 50px; } }
    &__row {
        display: flex;
        max-width: 1410px;
        margin: 0 auto;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 30px;
        &-s {
            max-width: 1260px;
            margin-bottom: 0; }
        @media(max-width: 1023px) {
            justify-content: center;
            align-items: center; } }
    &__item {
        @media(max-width: 1023px) {
            margin-right: 10px; }
        @media(max-width: 767px) {
            margin-right: 0;
            margin-bottom: 15px;
            width: 100%;
            display: flex;
            justify-content: center; } }
    &__img {
 }        // width: auto
    &__bottom {
        display: flex;
        margin-top: 50px;
        padding-top: 69px;
        border-top: 1px solid #edefef;
        justify-content: center;
        align-items: center;
        @media(max-width: 767px) {
            flex-direction: column; } }
    &__text {
        color: rgba(#16009e, 0.9);
        margin: 0;
        text-align: center;
        margin-right: 31px;
        @media(max-width: 767px) {
            margin-bottom: 20px; } }
    &__btn {
        border-radius: 50px;
        background-color: #3ac997;
        width: 219px;
        height: 61px;
        color: #ffffff;
        text-decoration: none;
        text-transform: uppercase;
        font-weight: bold;
        display: inline-block;
        line-height: 60px;
        cursor: pointer;
        text-align: center; } }



