
.header-wrapper {
  background: transparent;
  left: 0;
  padding: px-to-rem(50px) 0 79px 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: background .25s ease;
  z-index: 10;

  //   // new
  //   padding: 0 0 3rem
  @media(max-width: 1023px) {
    background: transparent !important;
    padding-bottom: px-to-rem(15px);
    padding-top: px-to-rem(25px);
    padding: px-to-rem(25px) px-to-rem(20px) px-to-rem(15px);
    position: absolute;

    .hamburger {
      position: fixed;
      right: px-to-rem(20px);
      top: px-to-rem(22px); } }


  &.fixed {
    animation: slideDown .6s ease;
    background: $dark;
    padding-bottom: px-to-rem(17px);
    padding-top: px-to-rem(17px);
    position: fixed;
    @media(max-width: 1023px) {
      animation: none;
      padding-top: px-to-rem(25px);
      position: absolute; }

    .header__anchor {
      display: none; } } }

.text-grad {
  color: #ef955d; }

.header {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 1640px;
  padding-left: px-to-rem(30px);
  padding-right: px-to-rem(30px);
  // padding: 0 115px 0 162px
  box-sizing: initial; }

