input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }

.nav {
  display: flex;
  font-size: px-to-rem(18px);
  font-weight: 500;
  letter-spacing: normal;
  margin-left: auto;
  width: auto;
  @media(max-width: 1023px) {
    bottom: 0;
    flex-direction: column;
    font-size: 14px;
    overflow: auto;
    position: fixed;
    right: -320px;
    top: 0;
    transition: .5s ease;
    width: 320px;

    &::before {
      background: rgba($black, .5);
      bottom: 0;
      content: '';
      left: 0;
      opacity: 0;
      position: fixed;
      right: 0;
      top: 0;
      transition: inherit;
      visibility: hidden; }

    &.opened {
      right: 0;

      &::before {
        opacity: 1;
        visibility: visible; } } }


  &__list {
    align-items: center;
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    @media(max-width: 1023px) {
      align-items: flex-start;
      background: $deep;
      flex-direction: column;
      height: 100%;
      overflow: auto;
      padding: 60px 20px 20px;
      position: relative; } }


  &__item {
    margin: 0 px-to-rem(40px) 0 0;
    transition: .3s ease;
    color: #1d1b1b;
    &:last-child {
      margin-right: 0;
      @media(max-width: 1023px) {
        margin-right: auto;
        @media(max-width: 1023px) {
          margin: 10px auto;
          text-align: center; }

        @media(min-width: 1024px) {
          &:first-child {
            margin-left: 40px;
            margin-right: auto;
            position: relative;

            &::before {
              border-left: 1px solid rgba(#707070, .3);
              bottom: px-to-rem(6px);
              content: '';
              left: -20px;
              top: px-to-rem(8px);
              position: absolute; } } }




        &:hover,
        &.active {
          color: $bluey-grey; }

        &.active {
          pointer-events: none; } } } }


  &__link {
    color: #1d1b1b;
    text-decoration: none; } }

