.hamburger {
  display: none;
  margin-left: auto;
  position: relative;
  width: 30px;
  z-index: 2;
  @media(max-width: 1023px) {
    display: block; }


  &__line {
    background: $light;
    border-radius: 3px;
    display: block;
    height: 3px;
    margin: 6px 0;
    transition: .5s ease; }

  &.opened {
    .hamburger__line {
      &:first-child {
        transform: rotate(45deg) translate(6px, 6px);
        transform-origin: center; }

      &:nth-child(2) {
        opacity: 0;
        transform: translateX(200%); }

      &:last-child {
        transform: rotate(-45deg) translate(6px, -6px);
        transform-origin: center; } } } }


