.input-drop,
.input-dollar,
.input-app {
  &::before {
    content: "";
    position: absolute;
    top: 18px;
    right: 20px;
    background: url(/static/img/arrDropDown.svg) 50% 50% no-repeat / auto; } }

.input-drop {
  &::before {
    width: 8px;
    height: 7px;
    background: url(/static/img/arrDropDown.svg) 50% 50% no-repeat / auto; } }
.input-dollar {
  &::before {
    background: url(/static/img/dollar.svg) 50% 50% no-repeat / auto;
    width: 17px;
    height: 15px; } }
.input-app {
  &::before {
    background: url(/static/img/show-apps-button.svg) 50% 50% no-repeat / auto;
    width: 13px;
    height: 13px; } }



