.profit {
    height: 495px;
    width: 100%;
    position: relative;
    padding-top: 70px;
    padding-bottom: 86px;
    background-color: #f5f9fa;
    z-index: 0;
    @media(max-width: 767px) {
        height: auto; }
    &::before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        width: 50%;
        z-index: -1;
        background-image: linear-gradient(to right, #ffbb37, #ba28ff 75%, #c00fc3);
        @media(max-width: 767px) {
            bottom: 0;
            top: auto;
            right: 0;
            left: 0;
            height: 50%;
            width: 100%; } }
    &__form {
        width: 100%; }
    &__inner {
        display: flex;
        width: 100%;
        height: 100%;
        // max-width: 1127px
        margin: 0 auto;
        @media(max-width: 767px) {
            display: block; } }
    &__right,
    &__left {
        display: flex;
        width: 50%;
        @media(max-width: 767px) {
            width: 100%; } }
    &__right {
        justify-content: flex-start;
        padding-left: 105px;
        display: flex;
        flex-direction: column;
        @media(max-width: 1023px) {
            padding-left: 35px; }
        @media(max-width: 767px) {
            padding-left: 0;
            position: relative;
            top: -30px; } }
    &__left {
        padding-right: 105px;
        justify-content: flex-end;
        @media(max-width: 1023px) {
            padding-right: 35px; }
        @media(max-width: 767px) {
            padding-bottom: 118px;
            padding-right: 0;
            margin-bottom: 60px; } }
    &__rect {
        max-width: 490px;
        width: 100%;
        margin-bottom: 8px;
        border-radius: 20px;
        // box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.25)
        border: solid 0 #ffffff;
        background-color: rgba(#793030, 0.15);
        color: #ffffff;
        display: flex;
        flex-direction: column;
        align-content: center;
        justify-content: center;
        align-items: flex-start;
        padding-left: 39px;
        // padding-right: 30px
        &:last-of-type {
            margin-right: 0;
            margin-bottom: 55px; }
        @media(max-width: 1023px) {
            padding-left: 20px; } }
    &__btn {
        background-color: #3ac997;
        white-space: nowrap;
        width: 100%;
        max-width: 490px;
        color: #ffffff;
        font-size: px-to-rem(18px);
        text-decoration: none;
        padding: 19px 69px 20px;
        border-radius: 50px;
        cursor: pointer;
        b {
            font-weight: bold; }
        @media(max-width: 1023px) {
            padding: 19px 20px 20px; }
        @media(max-width: 767px) {
            text-align: center; } }

    &__elipse {
        width: 83px;
        height: 83px;
        background-color: #000;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        &::before {
            content: "";
            width: 100%;
            position: absolute;
            top: 3px;
            border-radius: 50%;
            left: 0;
            display: block;
            height: 100%;
            background: url('/static/img/iconequally.svg') 50% 50% no-repeat / auto; }
        @media(max-width: 1023px) {
            width: 53px;
            height: 53px; } } }


.income {
    color: #ffffff;
    padding-top: 21px;
    padding-bottom: 27px;
    @media(max-width: 1023px) {
        padding-top: 14px;
        padding-bottom: 20px; }
    &__sub {
        font-size: px-to-rem(18px);
        font-weight: bold;
        margin-bottom: 10px; }
    &__total {
        font-size: px-to-rem(24px);
        b {
            margin-right: 10px;
            color: #ffdd28;
            font-weight: 900;
            font-size: px-to-rem(48px);
            @media(max-width: 1023px) {
                font-size: px-to-rem(40px); } }
        @media(max-width: 1023px) {
            font-size: px-to-rem(20px); } } }

.trader-r {
    display: flex;
    flex-direction: column;
    padding-top: 18px;
    padding-bottom: 26px;
    @media(max-width: 1023px) {
        padding-top: 11px;
        padding-bottom: 19px; }
    &__row {
        display: flex;
        align-items: center;
        margin-bottom: 15px;
        &:last-child {
            margin-bottom: 0; } }
    &__naming {
        font-size: 500px;
        color: #ffffff;
        opacity: 0.7;
        font-size: px-to-rem(18px); }
    &__name {
        font-size: px-to-rem(22px);
        color: #ffffff;
        font-weight: 900;
        @media(max-width: 1023px) {
            font-size: px-to-rem(20px); } }
    &__date {
        font-size: px-to-rem(22px);
        b {
            font-weight: 900; }
        @media(max-width: 1023px) {
            font-size: px-to-rem(20px); } } }
