.history-container {
    max-width: 451px;
    margin-left: auto;
    color: #101012;
    @media(max-width: 767px) {
        margin-left: initial; }
    &__item {
        font-size: px-to-rem(18px);
        padding-bottom: 34px;
        border-left: 2px dashed #3c7eff;
        position: relative;
        padding-top: 10px;
        padding-left: 46px;
        margin-left: 22px;
        @media(max-width: 767px) {
            padding-bottom: 45px; }
        &::before {
            position: absolute;
            left: -24px;
            top: 0;
            content: " ";
            font-size: px-to-rem(23px);
            border-radius: 50%;
            background: #3c7eff;
            color: #fff;
            height: 45px;
            width: 45px;
            display: flex;
            justify-content: center;
            align-items: center; }
        &:nth-child(1) {
            &::before {
                content: "1"; } }
        &:nth-child(2) {
            &::before {
                content: "2"; } }
        &:nth-child(3) {
            &::before {
                content: "3"; } }
        &:nth-child(4) {
            &::before {
                content: "4"; } }
        &:last-child {
            border: none;
            padding-bottom: 0; }
        &_ok {
            padding-bottom: 20px;
            &:nth-child(n) {
                &::before {
                    content: " ";
                    background: rgb(60, 126, 255)  url(/static/img/listOkBlue.svg) 50% 50% no-repeat/cover; } } } }
    &__title {
        margin-bottom: 12px;
        b {
            font-weight: 900; } }
    &__input-wr {
        height: 47px;
        position: relative; }
    &__input {
        border: none;
        font-size: 16px;
        padding: 13px 18px 15px 18px;
        background-color: transparent;
        border-radius: 5px;
        width: 100%;
        border: solid 1px #dddddd; } }










.customSelect,.custom-select-opener {
    height: 100%;
    padding: 0; }
.customSelect .custom-select-opener {
    font-size: 16px;
    padding: 13px 18px 15px 18px;
    background-color: transparent;
    border-radius: 5px;
    border: solid 1px #dddddd; }
.history-container__input-wr .datepicker {
    border: none;
    font-size: 16px;
    padding: 13px 18px 15px 18px;
    background-color: transparent;
    border-radius: 5px;
    width: 100%;
    border: solid 1px #dddddd;
    opacity: 1;
    position: static;
    box-shadow: none;
    box-sizing: border-box; }



