.rating {
    background-color: #1e2124;
    color: #ffffff;
    padding-top: 16px;
    padding-bottom: 89px;
    text-align: center;
    &__sub {
        font-size: px-to-rem(12px);
        opacity: 0.5;
        margin-bottom: 53px; }
    &__title {
        margin-bottom: 13px; }
    &__sub-title {
        opacity: 0.8;
        font-size: px-to-rem(18px);
        margin-bottom: 42px; }
    &__table {
        margin: 0 auto;
        max-width: 1589px;
        width: 100%;
        background: #2c2f33;
        margin-bottom: 32px;
        border-radius: 10px; }
    &__btn {
        font-size: px-to-rem(14px);
        width: 117px;
        height: 41px;
        color: #ffffff;
        text-decoration: none;
        border: solid 2px #56c69f;
        display: inline-block;
        border-radius: 28px;
        line-height: 40px;
        cursor: pointer; } }

.table-rating {
    padding: 40px 15px 19px;
    border-collapse: collapse;
    display: block;
    tbody {
        width: 100%;
        display: block; }
    tr {
        td {
            &:nth-child(1) {
                width: 6.10%;
                padding-left: 27px;
                @media(max-width: 767px) {
                    width: 16.1%; } }
            &:nth-child(2) {
                width: 19.6979232221523%; }
            &:nth-child(3) {
                width: 22.4%; }
            &:nth-child(4) {
                width: 19.8%; }
            &:nth-child(5) {
                width: 32.1%;
                @media(max-width: 767px) {
                    width: 20.1%; } } } }
    &__head {
        display: flex;
        width: 100%;
        th {
            text-align: left;
            color: #5a5e66;
            font-size: px-to-rem(14px);
            padding-bottom: 22px;
            &:nth-child(1) {
                width: 6.10%;
                padding-left: 27px;
                @media(max-width: 767px) {
                    width: 16.1%; } }
            &:nth-child(2) {
                width: 19.6979232221523%; }
            &:nth-child(3) {
                width: 22.4%; }
            &:nth-child(4) {
                width: 19.8%; }
            &:nth-child(5) {
                width: 32.1%;
                @media(max-width: 767px) {
                    width: 20.1%; } } } }
    &__row {
        width: 100%;
        display: flex;
        td {
            height: 80px;
            text-align: left;
            display: flex;
            align-items: center; }
        &:nth-child(odd) {
            background-color: #222528; } } }




