.top-wrapper {
  // background: url('/static/img/bgHead.svg') 50% -227px no-repeat
  background-size: auto 100%;
  // padding: 0 px-to-rem(30px)
  position: relative;
  z-index: 9;
  overflow: hidden;
  &__decore {
    position: absolute;
    width: 100%;
    max-width: 1054px;
    transform: translateX(-50%);
    left: 50%;
    top: -25%;
    z-index: -1;
    animation: topDecore 55s linear infinite;
    @media(max-width: 767px) {
      left: 138%; } } }


@keyframes topDecore {
  from {
    transform: translateX(-50%) rotate(0); }
  to {
    transform: translateX(-50%) rotate(180deg); } }
@keyframes topDecore2 {
  from {
    transform:  rotate(0); }
  to {
    transform:  rotate(180deg); } }

.top {
  &__inner {
    align-items: center;
    // display: flex
    flex-direction: column;
    // height: px-to-rem(884px)
    margin: 0 auto;
    max-width: px-to-rem(1640px);
    box-sizing: initial;
    padding-top: px-to-rem(166px);
    // padding-left: px-to-rem(30px)
    // padding-right: px-to-rem(30px)
    // @media(max-width: 1870px)
    //   padding-top: px-to-rem(130px)
    //   max-width: initial

    // @media(max-width: 1024px)
    //   padding-top: px-to-rem(230px)

    // @media(max-width: 767px)
    //   height: auto
 }    //   padding-top: 100px


  &__title {
    text-align: left;
    font-size: px-to-rem(48px);
    font-weight: bold;
    color: #16009e;
    margin-bottom: 20px;
    @media(max-width: 767px) {
      font-size: px-to-rem(40px);
 } }      // text-align: center
  &__sub-title {
    display: block;
    font-size: px-to-rem(36px);
    font-weight: normal;
    @media(max-width: 767px) {
      font-size: px-to-rem(30px); } }
  &__description {
    color: #000000;
    font-size: px-to-rem(24px);
    margin-bottom: 100px;
    @media(max-width: 1023px) {
      margin-bottom: 50px; }
    @media(max-width: 767px) {
      font-size: px-to-rem(18px); } }
  &__img {
    // position: absolute
    position: relative;
    top: 0;
    right: -14px;
    max-width: 774px;
    margin-top: -295px;
    max-height: 531px;
    // @media(max-width: 1666px)
    @media(max-width: 1690px) {
      width: 40%;
      height: 50%;
      right: 0; }
    @media(max-width: 1439px) {
      margin: 0;
      width: 50%; }
    @media(max-width: 1023px) {
      display: none; } }
  &__btn {
    width: 219px;
    height: 60px;
    margin: 0 auto;
    // margin-top: -35px
    top: -30px;

    box-sizing: border-box;
    position: relative;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;

    justify-content: center;
    align-items: center;

    // border: 5px solid  #00ead7
    border-radius: 50px;

    background-color: #3ac997;
    box-shadow: 0px 12px 20px -11px #3ac997;

    font-size: px-to-rem(18px);
    color: #fff;
    line-height: 50px;
    text-decoration: none;
    font-weight: bold;
    text-transform: uppercase;

    &:hover {
      .top__btn_img {
        transform: translateX(-5px); }

      .top__btn_span {
        transform: translateX(5px); } }

    @media(max-width: 767px) {
      width: 100%;
      max-width: 200px;
 }      // margin: 0 10% 20px
    .border {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      box-sizing: initial;
      z-index: -1;
      background-color: #f5f9fa;
      border-radius: 40px;
      border: 20px solid #f5f9fa;
      margin-top: -20px; } }

  &__scroll-btn {
    margin: px-to-rem(5px) 0 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    align-items: center;
    @media(max-width: 767px) {
 }      // margin: 35px 0 40px

    &-text {
      margin-bottom: 14px;
      color: #000000;
      font-size: px-to-rem(14px);
      opacity: 0.5; } }

  &__bottom {
    background-color: #f5f9fa;
    z-index: -1; } }


.subtract {
  padding: 66px 0 100px;
  background-image: linear-gradient(to right, #ffbb37, #ba28ff 51%, #c00fc3);
  position: relative;
  z-index: -1;
  &__inner {
    margin: 0 auto;
    max-width: px-to-rem(1640px);
    // padding-left: px-to-rem(30px)
    // padding-right: px-to-rem(30px)
    // padding: 0 115px 0 162px
    display: flex;
    position: relative;
    box-sizing: initial; }
  &__items {
    width: 64%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    @media(max-width: 1023px) {
      width: 100%; }
    @media(max-width: 767px) {
      justify-content: center; } } }




